<template>
  <div class="page-container">
    <div class="tab-wrapper">
      <vue-tabs-chrome v-model="tabCurrent" :tabs="tabs" />
    </div>
    <div class="page-section info-tab-display" v-if="tabCurrent == 'info'">
      <div class="report-sheet report-sheet-info" style="grid-row: span 2">
        <div class="report-container">
          <div class="sheet-body" style="grid-template-columns: 50% 50%">
            <div class="section-label" style="grid-column: span 2">
              <label>piping information</label>
            </div>
            <div class="form-item" v-for="item in generalInfo" :key="item.desc">
              <div class="form-item-label">
                <label>{{ item.desc }}</label>
              </div>
              <div class="form-item-value">
                <label>{{ item.value }}</label>
              </div>
            </div>
            <!-- <div class="form-item" style="grid-column: span 2; grid-row: span 2; height: 49px">
              <div class="form-item-label" style="height: 36px">
                <label>-</label>
              </div>
              <div class="form-item-value" style="height: 36px">
                <label>placeholder</label>
              </div>
            </div>-->
          </div>
        </div>
      </div>
      <div class="report-sheet">
        <div class="report-container">
          <div class="sheet-body">
            <div>
              <div class="section-label">
                <label>overview picture</label>
              </div>

              <div class="form-item-picture-log">
                <div class="img-box">
                  <div class="btn-panel">
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="PREVIEW_PIC(infoPiping.overview_img_path)"
                      v-if="infoPiping.overview_img_path"
                    >
                      <i class="las la-eye"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="DELETE_PIC(infoPiping.overview_img_path, 1)"
                      v-if="infoPiping.overview_img_path"
                    >
                      <i class="las la-trash"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button>
                      <label
                        class="pic-toolbar-btn"
                        for="pic-upload-btn"
                        v-if="!infoPiping.overview_img_path"
                      >
                        <i class="las la-plus"></i>
                      </label>
                    </v-ons-toolbar-button>
                  </div>
                  <img
                    :src="baseURL + infoPiping.overview_img_path"
                    v-if="infoPiping.overview_img_path"
                  />
                  <input
                    type="file"
                    id="pic-upload-btn"
                    style="display: none"
                    ref="pic_upload_ov"
                    @change="UPLOAD_PIC(1)"
                  />

                  <div class>
                    <i class="las la-image" v-if="!infoPiping.overview_img_path"></i>
                    <label v-if="!infoPiping.overview_img_path">No Image</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="report-sheet">
        <div class="report-container">
          <div class="sheet-body">
            <div>
              <div class="section-label">
                <label>name plate picture</label>
              </div>

              <div class="form-item-picture-log">
                <div class="img-box">
                  <div class="btn-panel">
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="PREVIEW_PIC(infoPiping.name_plate_img_path)"
                      v-if="infoPiping.name_plate_img_path"
                    >
                      <i class="las la-eye"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="DELETE_PIC(infoPiping.name_plate_img_path, 2)"
                      v-if="infoPiping.name_plate_img_path"
                    >
                      <i class="las la-trash"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button v-if="!infoPiping.name_plate_img_path">
                      <label
                        class="pic-toolbar-btn"
                        for="pic-upload-btn-2"
                        v-if="!infoPiping.name_plate_img_path"
                      >
                        <i class="las la-plus"></i>
                      </label>
                    </v-ons-toolbar-button>
                  </div>
                  <img
                    :src="baseURL + infoPiping.name_plate_img_path"
                    v-if="infoPiping.name_plate_img_path"
                  />
                  <input
                    type="file"
                    id="pic-upload-btn-2"
                    style="display: none"
                    ref="pic_upload_np"
                    @change="UPLOAD_PIC(2)"
                  />
                  <i class="las la-image" v-if="!infoPiping.name_plate_img_path"></i>
                  <label v-if="!infoPiping.name_plate_img_path">No Image</label>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="tabCurrent == 'const'">
      <constTable />
    </div>
    <div v-if="tabCurrent == 'insp'">
      <inspTable />
    </div>
    <div v-if="tabCurrent == 'gen'">
      <genTable />
    </div>

    <contentLoading text="Loading, please wait..." v-if="isLoading == true" color="#fc9b21" />
    <previewImage :imageURL="previewImg" v-if="previewImg" @close-preview="PREVIEW_PIC_CLOSE()" />
  </div>
</template> 

<script>
//API
import axios from "/axios.js";
import moment from "moment";

//Components
import VueTabsChrome from "vue-tabs-chrome";
import contentLoading from "@/components/app-structures/app-content-loading.vue";
import previewImage from "@/components/image-preview.vue";
import constTable from "@/views/Applications/Piping/Pages/Information/table-const.vue";
import inspTable from "@/views/Applications/Piping/Pages/Information/table-insp.vue";
import genTable from "@/views/Applications/Piping/Pages/Information/table-general.vue";

//DataGrid

export default {
  name: "PipingInformation",
  components: {
    VueTabsChrome,
    contentLoading,
    previewImage,
    constTable,
    inspTable,
    genTable
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Piping Information",
      subpageInnerName: null
    });
    this.FETCH_INFO();
  },
  data() {
    return {
      infoPiping: {
        design_temp_max_c: "-",
        design_temp_min_c: "-",
        design_temp_max_f: "-",
        design_temp_min_f: "-"
      },
      file_pic_upload: "",
      tabCurrent: "info",
      tabs: [
        {
          label: "General Information",
          key: "info",
          closable: false
        },
        {
          label: "Construction Document",
          key: "const",
          closable: false
        },
        {
          label: "Inspection Report",
          key: "insp",
          closable: false
        },
        {
          label: "General Document",
          key: "gen",
          closable: false
        }
      ],
      previewImg: "",
      isLoading: false
    };
  },
  computed: {
    generalInfo() {
      var info = [
        {
          desc: "Tag No.",
          value: this.infoPiping.id
        },
        {
          desc: "Insulation",
          value: this.infoPiping.insulation
        },
        {
          desc: "Line No.",
          value: this.infoPiping.line_no
        },
        {
          desc: "Insulation Type",
          value: this.infoPiping.insulation_type
        },
        {
          desc: "Site Name",
          value: this.infoPiping.site_name
        },
        {
          desc: "Insulation Size (mm)",
          value: this.infoPiping.insulation_size
        },
        {
          desc: "From",
          value: this.infoPiping.line_from
        },
        {
          desc: "Design Corrosion Allowance (mm)",
          value: this.infoPiping.CAd
        },
        {
          desc: "To",
          value: this.infoPiping.line_to
        },
        {
          desc: "Design Life (year)",
          value: this.infoPiping.Ld
        },
        {
          desc: "NPS (inch)",
          value: this.infoPiping.nps
        },
        {
          desc: "Design Pressure (barg)",
          value: this.infoPiping.design_pressure_barg
        },
        {
          desc: "P&ID No.",
          value: this.infoPiping.pid_no
        },
        {
          desc: "Design Pressure (psi)",
          value: this.infoPiping.design_pressure_psi
        },
        {
          desc: "Drawing No.",
          value: this.infoPiping.drawing_no
        },
        {
          desc: "Operating Pressure (barg)",
          value: this.infoPiping.operating_pressure_barg
        },
        {
          desc: "In-service Date",
          value: this.infoPiping.inservice_date
            ? moment(this.infoPiping.inservice_date).format("LL")
            : null
        },
        {
          desc: "Operating Pressure (psi)",
          value: this.infoPiping.operating_pressure_psi
        },
        {
          desc: "Design Code",
          value: this.infoPiping.piping_code
        },
        {
          desc: "Design Temperature (C)",
          value: this.infoPiping.design_temp_max_c
        },
        {
          desc: "Pipe Spec",
          value: this.infoPiping.spec_class
        },
        {
          desc: "Design Temperature (F) ",
          value: this.infoPiping.design_temp_max_f
        },
        {
          desc: "Service",
          value: this.infoPiping.service_desc
        },
        {
          desc: "Operating Temperature (C)",
          value: this.infoPiping.operating_temp_c
        },
        {
          desc: "Material",
          value: this.infoPiping.material_name
        },
        {
          desc: "Operating Temperature (F)",
          value: this.infoPiping.operating_temp_f
        },
        {
          desc: "Stress (psi)",
          value: this.infoPiping.stress_psi
        },
        {
          desc: "Inspection Interval (year)",
          value: this.infoPiping.ins_interval
        },
        {
          desc: "Joint Efficiency",
          value: this.infoPiping.e
        },
        {
          desc: "Inventory Group",
          value: this.infoPiping.inventory_group_name
        },
        {
          desc: "Dead Leg",
          value: this.infoPiping.deadleg
        },
        {
          desc: "Service Class",
          value: this.infoPiping.service_class
        },
        {
          desc: "System",
          value: this.infoPiping.system_name
        },
        {
          desc: "Piping Group",
          value: this.infoPiping.piping_group_name
        }
      ];
      return info;
    },
    baseURL() {
      var mode = this.$store.state.mode;
      if (mode == "dev") return this.$store.state.modeURL.dev;
      else if (mode == "prod") return this.$store.state.modeURL.prod;
      else return console.log("develpment mode set up incorrect.");
    }
  },
  watch: {
    // watch for changes in the $route object
    $route: {
      handler() {
        // run this logic whenever router is change
        this.FETCH_INFO();
      }
      //immediate: true // call the handler immediately when the component is created
    }
  },
  methods: {
    FETCH_INFO() {
      //console.log("==> FETCH PIPING INFO: ");
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;

      axios({
        method: "get",
        url: "/PipingInfo/piping-info-view-by-id?id_piping=" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.infoPiping = res.data[0];
            this.infoPiping.minmax_temp_c =
              this.infoPiping.design_temp_min_c !== null &&
              this.infoPiping.design_temp_max_c !== null
                ? this.infoPiping.design_temp_min_c +
                  "/" +
                  this.infoPiping.design_temp_max_c
                : "-/-";

            this.infoPiping.minmax_temp_f =
              this.infoPiping.design_temp_min_f !== null &&
              this.infoPiping.design_temp_max_f !== null
                ? this.infoPiping.design_temp_min_f +
                  "/" +
                  this.infoPiping.design_temp_max_f
                : "-/-";
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    UPLOAD_PIC(pic_type) {
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;
      if (pic_type == 1) {
        this.file_pic_upload = this.$refs.pic_upload_ov.files[0];
      } else {
        this.file_pic_upload = this.$refs.pic_upload_np.files[0];
      }
      if (
        (this.file_pic_upload && this.file_pic_upload.type == "image/jpeg") ||
        this.file_pic_upload.type == "image/png"
      ) {
        if (this.file_pic_upload.size < 20000000) {
          axios({
            method: "post",
            url: "/PipingInfo/upload-pic",
            headers: {
              "Content-Type": "multipart/form-data",
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id: id_tag,
              file: this.file_pic_upload,
              type: pic_type
            }
          })
            .then(res => {
              if (res.status == 200) {
                this.FETCH_INFO();
                //console.log("==> PIPING INFO IMG UPLOAD: DONE");
              }
            })
            .catch(error => {
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {
              this.isLoading = false;
            });
        } else {
          this.$ons.notification.alert("File size too large. (20 MB max)");
          const file = document.getElementById("pic-upload-btn");
          file.val = "";
          this.isLoading = false;
        }
      } else {
        this.$ons.notification.alert(
          "Incorrect filetype. <br/> Only JPEG/PNG file can be uploaded."
        );
        const file = document.getElementById("pic-upload-btn");
        file.val = "";
        this.isLoading = false;
      }
    },
    DELETE_PIC(file_path, pic_type) {
      this.$ons.notification.confirm("Confirm delete?").then(res => {
        if (res == 1) {
          this.isLoading = true;
          console.log("==> PIPING INFO IMG DELETE: START");
          const id_tag = this.$route.params.id_tag;
          console.log("==> ID TAG (PIPING): " + id_tag);
          axios({
            method: "delete",
            url: "/PipingInfo/delete-pic/",
            headers: {
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id: id_tag,
              file_path: file_path,
              type: pic_type
            }
          })
            .then(res => {
              console.log(res);

              if (res.status == 200) {
                this.FETCH_INFO();
                console.log("==> PIPING INFO IMG DELETE: DONE");
                this.isLoading = false;
              }
            })
            .catch(error => {
              this.isLoading = false;
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {
              this.FETCH_INFO();
            });
        }
      });
    },
    PREVIEW_PIC(img) {
      if (img) {
        this.previewImg = img;
      }
    },
    PREVIEW_PIC_CLOSE() {
      this.previewImg = "";
    }
  }
};
</script>

