<template>
  <div class="page-section">
    <div class="table-header-toolbar left" style="width: calc(100% - 231px)">
      <label id="hd">General Document</label>
    </div>
    <DxDataGrid
      id="const-data-grid"
      key-expr="id"
      :data-source="library"
      :element-attr="dataGridAttributes"
      :hover-state-enabled="true"
      :allow-column-reordering="true"
      :show-borders="true"
      :show-row-lines="true"
      :focused-row-enabled="false"
      :row-alternation-enabled="false"
      @row-inserted="ADD_NEW_FILE"
      @row-removed="DELETE_DOC"
      @init-new-row="() => { this.file=[] }"
    >
      <DxEditing
        :allow-deleting="true"
        :allow-adding="true"
        :allow-updating="true"
        :use-icons="true"
        :show-borders="true"
        mode="form"
      >
        <DxForm label-location="top"></DxForm>
      </DxEditing>

      <DxColumn data-field="file" :visible="false" edit-cell-template="insertCellTemplate" />
      <!-- <DxColumn data-field="file" :visible="false" /> -->
      <DxColumn
        data-field="file_name"
        :allow-adding="true"
        :allow-editing="true"
        caption="File name"
        :editor-options="fileNameInputOptions"
      />

      <template #editCellTemplate="{  }">
        <div>
          <img src="http://tmt-solution.com/public/image-empty.png" width="150" height="100" />
          <DxFileUploader
            select-button-text="Select File"
            label-text
            upload-mode="useForm"
            @value-changed="VALUE_CHANGE"
          />
        </div>
      </template>

      <template #insertCellTemplate>
        <div class="widget-container">
          <DxFileUploader
            id="file-uploader"
            :multiple="false"
            upload-mode="useForm"
            @value-changed="VALUE_CHANGE"
            :visible="true"
          />
        </div>
      </template>

      <DxColumn type="buttons">
        <DxButton name="image" hint="download" icon="download" :on-click="DOWNLOAD" />
        <!-- <DxButton name="edit" hint="Edit" icon="edit" /> -->
        <DxButton name="delete" hint="Delete" icon="trash" />
      </DxColumn>

      <DxHeaderFilter :visible="true" />
      <!-- <DxFilterRow :visible="false" /> -->
      <DxScrolling mode="standard" />
      <DxSearchPanel :visible="true" />
      <DxPaging :page-size="10" :page-index="0" />
      <DxPager
        :visible="false"
        :show-page-size-selector="true"
        :allowed-page-sizes="[5, 10, 'all']"
        :show-navigation-buttons="true"
        :show-info="true"
        info-text="Page {0} of {1} ({2} items)"
      />
      <DxExport :enabled="false" />
      <DxForm>
        <DxItem></DxItem>
      </DxForm>
    </DxDataGrid>
  </div>
</template>

<script>
import axios from "/axios.js";
import { DxFileUploader } from "devextreme-vue/file-uploader";
//import DxForm from "devextreme-vue/form";
import "devextreme/dist/css/dx.light.css";
import {
  DxDataGrid,
  DxEditing,
  DxSearchPanel,
  DxPaging,
  DxPager,
  DxScrolling,
  DxColumn,
  DxExport,
  //DxToolbar,
  DxItem,
  //DxFormItem,
  DxButton,
  DxHeaderFilter,
  DxForm
} from "devextreme-vue/data-grid";

export default {
  name: "LibraryGeneral",
  components: {
    DxDataGrid,
    DxEditing,
    DxSearchPanel,
    DxPaging,
    DxPager,
    DxScrolling,
    DxColumn,
    DxExport,
    //DxToolbar,
    DxItem,
    //DxFormItem,
    DxForm,
    DxButton,
    DxHeaderFilter,
    DxFileUploader
  },
  created() {
    console.clear();
    this.FETCH_LIBRARY();
  },
  data() {
    return {
      file: [],
      library: [],
      dataGridAttributes: {
        class: "data-grid-style"
      },
      fileNameInputOptions: { placeholder: "Enter file name" }
    };
  },
  computed: {
    baseURL() {
      var mode = this.$store.state.mode;
      if (mode == "dev") return this.$store.state.modeURL.dev;
      else if (mode == "prod") return this.$store.state.modeURL.prod;
      else return console.log("develpment mode set up incorrect.");
    }
  },
  methods: {
    FETCH_LIBRARY() {
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;
      axios({
        method: "get",
        url: "/PipingLibrary/get-library-by-type?id=" + id_tag + "&type=3",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          //console.log(res);
          if (res.status == 200) {
            //console.log("in");
            this.library = res.data;
            this.isLoading = false;
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    ADD_NEW_FILE(e) {
      const user = JSON.parse(localStorage.getItem("user"));
      var formData = new FormData();
      formData.append("id", 0);
      formData.append("id_line", this.$route.params.id_tag);
      formData.append("file_name", e.data.file_name ?? "");
      formData.append("id_type", 3);
      formData.append("created_by", user.id);
      formData.append("file", this.file);
      if (this.file.length == 0)
        return this.$ons.notification.alert("Please select file").then(res => {
          if (res == 0) {
            this.FETCH_LIBRARY();
          }
        });
      axios({
        method: "post",
        url: "/PipingLibrary",
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: formData
      })
        .then(res => {
          console.log(res);
          if (res.status == 201) {
            this.FETCH_LIBRARY();
          }
        })
        .catch(error => {
          console.log(error);
          this.isLoading = false;
          this.$ons.notification
            .alert(error.message, {
              title: "Add New File failed"
            })
            .then(res => {
              if (res == 0) {
                this.FETCH_LIBRARY();
              }
            });
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    VALUE_CHANGE(e) {
      //console.log("fileReader e data:");
      console.log(e);
      console.log(e.value[0].name);
      let reader = new FileReader();
      reader.readAsDataURL(e.value[0]);
      reader.onload = () => {
        // this function is for showing the image preview on upload
      };
      this.file = e.value[0];
    },
    DOWNLOAD(e) {
      const url = this.baseURL + e.row.data.file_path;
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", e.row.data.file_name);
      document.body.appendChild(link);
      link.click();
    },
    DELETE_DOC(e) {
      const id = e.data.id;
      axios({
        method: "delete",
        url: "/PipingLibrary/" + id,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        }
      })
        .then(res => {
          if (res.status == 204) {
            this.FETCH_LIBRARY();
          }
        })
        .catch(error => {
          console.log(error);
          this.isLoading = false;
          this.$ons.notification
            .alert(error.message, {
              title: "Delete failed"
            })
            .then(res => {
              if (res == 0) {
                this.FETCH_LIBRARY();
              }
            });
        })
        .finally(() => {
          this.isLoading = false;
        });
    }
  }
};
</script>

